import React from "react";
import Title from "react-vanilla-tilt";
import "./SearchBar.css";
import NoPoster from "./NoPoster.gif";

function SearchCard({
  title,
  language,
  vote_average,
  original_title,
  release_date,
  genres,
  overview,
  imageUrl,
  movie_id,
  backdrop_path,
}) {
  const modal = document.getElementById("myModal");
  const span = document.getElementsByClassName("close")[0];

  function hendeleModelClick() {
    modal.style.display = "block";
  }
  window.onclick = function (event) {
    if (event.target === modal) {
      modal.style.display = "none";
    }
  };

  return (
    <>
      <Title
        style={{
          backgroundColor: "rgba(255, 255, 255, 0)",
          padding: "0px",
          borderRadius: "15px",
        }}
      >
        <div className="seacard">
          <div className="contents">
            {imageUrl ? (
              <img
                className="img"
                src={`https://image.tmdb.org/t/p/w200` + imageUrl}
                alt={`${title} Poster`}
              ></img>
            ) : (
              <img
                src={
                  "https://image.tmdb.org/t/p/w500/kqjL17yufvn9OVLyXYpvtyrFfak.jpg"
                }
              ></img>
            )}
            <h3 id="title">{title}</h3>
            <div className="spn">
              <span>{vote_average}</span>
              <span>{release_date} </span>
            </div>
          </div>
          <div className="hoverContainer">
            <div className="hoverContaent row">
              <h4>{title}</h4>
              
              <img
                className="img1"
                src={`https://image.tmdb.org/t/p/w200` + imageUrl}
                alt={`${title} Poster`}
              ></img>
              <img
                className="img2"
                src={`https://image.tmdb.org/t/p/w200` + backdrop_path}
                alt={`${title} Poster`}
              ></img>
              <p>{overview}</p>
            </div>
          </div>
        </div>
      </Title>
    </>
  );
}

export default SearchCard;
