import './App.css';
import React, { useState } from 'react'
import Footer from './component/Footer';
import Movie from './component/Movie';
import Navbar from './component/Navbar';
import ScrollTotop from './component/ScrollTotop';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import SearchBar from './component/SearchBar';



const App=()=> {
  const [pageNo, setPageNo] = useState(1);
  const [totalPage, setTotalPage] = useState();

  const apiKey =process.env.REACT_APP_TMDB_API
  const tmdb_Url ="https://api.themoviedb.org/3/"
  const searchUrl = "search/movie"
  require('dotenv').config()
  const [language, setLanguage] = useState("de-DE");
  
  return (
    <>
        <Router> 
          <Navbar searchUrl={searchUrl} language={language} setLanguage={setLanguage}/>
          <Switch>
            <Route exact path="/">     
              <Movie totalPage={totalPage} setTotalPage={setTotalPage} apiKey={apiKey} pageNo={pageNo} setPageNo={setPageNo} baseurl={tmdb_Url} language={language} setLanguage={setLanguage}/>
            </Route>
            <Route exact path="/searchBar">
              <SearchBar setTotalPage={setTotalPage}  totalPage={totalPage} apiKey={apiKey} setPageNo={setPageNo} pageNo={pageNo}/>
            </Route>
          </Switch>
        </Router>

        <ScrollTotop/>
        <Footer/>
    </>
  );
}

export default App;
